function ClawScreen(){

const controlData = {
    'action': 'control',
    'unique': '',
    'up': false,
    'down': false,
    'left': false,
    'right': false,
    'claw': false
  };
  
  const sendControls = () => {
    socket.send( JSON.stringify( controlData ) );
  };
  
  const sendControl = ( whichControl, newValue ) => {
    controlData[ whichControl ] = newValue;
    sendControls();
  };

  const navigate = useNavigate();
  const handleClick = () => navigate('/complete');

  const [startTime, setStartTime] = useState(new Date().getTime() / 1000);
  const [counter, setCounter] = useState(59);
  useEffect(() => {
    localStorage.setItem("hasPlayed", "true");
    if (!socket)
    {
      navigate('/');
    } 

    const timer =
      counter > 0 && setInterval(() => 
      {
        console.log((Math.round(new Date().getTime() / 1000) - startTime));
        setCounter(59 - Math.round(new Date().getTime() / 1000 - startTime));
      }
      , 1000);
      if (timer <= 0){
        navigate('/complete')}
    return () => clearInterval(timer);
  }, [counter]);

  function preventLongPress(){
    console.log('preventLongPress Called')
      document.querySelectorAll('.prevent-context').forEach((el) => {
        console.log('found class')
        el.oncontextmenu = function(event) {
        event.preventDefault();
        event.stopPropagation();
        event.stopImmediatePropagation();
        return false;
      };
     })
    }

  useEffect(() => {
    // if (!hasPassthrough)
    // {
    //   navigate('/error')
    // }

    preventLongPress();
    setStartTime(new Date().getTime() / 1000);
  }, []);

  return (
    <>
    <div className='scanMessage prevent-context'>MOVE THE CLAW WITH YOUR PHONE</div>
    <div className='controlPad prevent-context'>
    <div className="countdown prevent-context">
      :{counter}
    </div>

    <div className="buttonClawUp prevent-context">
      <button className="buttonSquare prevent-context" id="buttonUp" onPointerDown={() => sendControl('up', true)} onPointerUp={() => sendControl('up', false)}>
        {/* <img src={'./arrow_up.png'} className="arrow prevent-context" alt="logo" /> */}
      </button>
      <button className="buttonSquareShadow prevent-context" ></button>
    </div>
    <div className="buttonClawDown prevent-context"> 
      <button className="buttonSquare prevent-context" id="buttonDown" onPointerDown={() => sendControl('down', true)} onPointerUp={() => sendControl('down', false)}>
      </button>
      <button className="buttonSquareShadow prevent-context"></button>
    </div>

    <div className="buttonClawLeft prevent-context">
      <button className="buttonSquare prevent-context" id="buttonLeft" onPointerDown={() => sendControl('left', true)} onPointerUp={() => sendControl('left', false)}>
      </button>
      <button className="buttonSquareShadow prevent-context"></button>
    </div>
    <div className="buttonClawRight prevent-context">
      <button className="buttonSquare prevent-context" id="buttonRight" onPointerDown={() => sendControl('right', true)} onPointerUp={() => sendControl('right', false)}>
      </button>
      <button className="buttonSquareShadow prevent-context"></button>
    </div>
    </div>

    <div className="buttonContainerClaw prevent-context">
      <button className="buttonMainClaw prevent-context"
      onMouseDown={() => sendControl('claw', true)}
      onClick={handleClick}
      >DROP THE CLAW!</button>
      <button className="buttonMainShadowClaw prevent-context"></button>
    </div>
    </>
  );
}

